h3 {
    color: black;
    font-size: 20px;
    font-weight: 520;
}

p {
    font-size: 18px;
    color: grey;
    line-height: 28px;
}

a {
    color: black;
    text-decoration: none;
}

a:link, a:visited {
    color: black !important;
}

a:hover {
    color: black !important;
}

ul {
    padding-left: 20px;
}

li {
    margin: 10px;
    margin-left: 0;
    color: grey;
    list-style-type: circle;
}

.navigation {
    height: 100%;
    width: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.infomation {
    margin: 15px 15px;
}

.siteName {
    color: grey;
}

.technewsSiteName {
    margin: 1vh 0;
    font-size: 15px;
}


.demo-infinite-container {
    border: 1px solid #e8e8e8;
    border-radius: 4px;
    overflow: auto;
    padding: 8px 24px;
    height: 90vh;
  }
  
  .demo-loading-container {
    position: absolute;
    bottom: 40px;
    width: 100%;
    text-align: center;
  }